body {
  margin: 0;
  background-color: #c8d0d2;
}

.container {
  /* max-width: 900px; */
  margin: 1rem 2rem;
}


/* HEADER  */
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1.5rem;
  flex-wrap: wrap;
  background-color: #fff;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.3);
}

.form-group {
  display: flex;
  flex-direction: column;
  margin: 0.5rem;
}

.form-group > label {
  color: #777;
  font-size: 0.75rem;
  margin-bottom: 0.25rem;
}

.btn {
  background-color: hsl(200, 100%, 50%);
  color: #fff;
  padding: 0.5em 1em;
  border-radius: 0.3rem;
  border: none;
  cursor: pointer;
}

.btn:hover {
  background-color: hsl(200, 100%, 40%);
}


/* FLASHCARD   */
.card-grid {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: .25rem;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.3);
  /* height: 280px; */

  /* card flip effect  */
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  transition: 150ms;
  transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
  cursor: pointer;
}

.card:hover {
  --translate-y: -2px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5);  
}

.card.flip {
  --rotate-y: 180deg;
}

.card .front {
  left: 0;
}

.card .back {
  transform: rotateY(180deg);
}

.card .front,
.card .back {
  position: absolute;
  padding: 1rem;
  backface-visibility: hidden;
  background-color: #fff;
}

.flashcard-options {
  margin-top: 0.5rem;
}

.flashcard-option {
  margin-top: .25rem;
  color: #555;
  font-size: .75rem;
}

.flashcard-option:first-child {
  margin-top: 0;
}